.select {
  min-width: 160px;
  padding: 6px 32px 6px 12px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.14);
  background: rgba(15, 23, 42, 0.55);
  color: var(--text, #f8fafc);
  font-size: 13px;
  font-weight: 600;
  line-height: 1.3;
  cursor: pointer;
  transition: border-color 120ms ease, box-shadow 120ms ease, background 120ms ease;
}

.select:hover {
  border-color: rgba(124, 92, 255, 0.7);
  background: rgba(15, 23, 42, 0.7);
}

.select:focus {
  outline: none;
  border-color: rgba(124, 92, 255, 0.95);
  box-shadow: 0 0 0 2px rgba(124, 92, 255, 0.25);
}

.select option {
  color: #0f172a;
}

.compact {
  min-width: 130px;
  padding: 4px 28px 4px 10px;
  font-size: 12px;
}

.optionSuccess {
  background: linear-gradient(100deg, rgba(34, 197, 94, 0.18), rgba(21, 128, 61, 0.08));
  color: #16a34a !important;
  font-weight: 600;
}

.optionSuccess:checked,
.optionSuccess:hover {
  background: linear-gradient(100deg, rgba(34, 197, 94, 0.28), rgba(21, 128, 61, 0.18));
}
