:global(:root) {
  /* dark trading dashboard palette (local overrides) */
  --bg: #07101a;
  --card-bg: #0f1720;
  --text: #e6eef8;
  --muted: #9aa6b2;
  --primary: #7c5cff;
  --accent: #22c1c3; /* teal-ish */
  --danger: #ff6b6b;
  --muted-border: rgba(255, 255, 255, 0.04);
}

/* New layout wrapper for list + detail inside the right column */
.listAndDetailWrap {
  display: grid;
  /* left column slightly wider and kept consistent across tabs */
  /* moderate width so cards reach the right-side filter buttons without over-expanding */
  grid-template-columns: clamp(460px, 46vw, 820px) 1fr;
  gap: 16px;
  align-items: start;
  width: 100%;
}

.listOnly,
.cardFullBleedTrade .listOnly,
.tradesArea .listOnly {
  grid-template-columns: 1fr !important;
}

/* When used inside a full-bleed Card we want the left list to claim more horizontal space
   so trades can show more fields and badges without excessive wrapping. This class is
   applied to the Card via the `className` prop from TradeJournal. */
.cardFullBleedTrade .listAndDetailWrap {
  /* Make the left list column take the flexible remaining space and
     allocate a smaller fixed column for the optional detail/controls on the right.
     This makes the internal trade-list card expand to the right edge of the Card. */
  /* reduce right detail column so list gets more horizontal space */
  grid-template-columns: 1fr 280px;
}

.cardFullBleedTrade .leftPane {
  padding: 10px; /* slightly reduce inner padding to use more width */
}

/* When rendering without Card (tradesArea) remove card-like background/shadow
   so the list appears flush with the outer container and uses full width. */
.tradesArea .leftPane {
  background: transparent;
  box-shadow: none;
  border-radius: 0;
  padding: 0;
  min-height: 0;
}

.tradesArea .listAndDetailWrap {
  /* tradesArea renders without Card; give the detail pane less width so
     the left (list) pane becomes noticeably wider */
  grid-template-columns: 1fr 280px;
}

/* Make table full-bleed inside tradesArea (remove rounded card container) */
.tradesArea .table {
  background: transparent;
  box-shadow: none;
  border-radius: 0;
}

.tradesArea .tableWrap {
  padding: 0;
  max-height: 72vh; /* keep scrolling behaviour */
}

/* Aggressive override to ensure no card visuals remain in the trades area.
   This helps identify whether the new markup is being loaded in the browser. */
.tradesArea .table,
.tradesArea .table *,
.tradesArea .positionsList,
.tradesArea .positionsList * {
  /* restrict the aggressive 'no-card' visual reset to the table/list area only
     so control elements (buttons, filters, tabs) keep their intended styles */
  background: transparent !important;
  box-shadow: none !important;
  border-radius: 0 !important;
}

/* Ultra-wide modifier: makes the left (list) pane even wider by shrinking the
   right/detail pane further. Toggle this class on the trades wrapper to enable. */
.ultraWide .listAndDetailWrap {
  /* hide the right/detail pane entirely so the list claims full width */
  grid-template-columns: 1fr 0px;
}

/* Minimal styles for the toggle button shown in the trades controls */
.ultraWideToggle {
  background: rgba(255,255,255,0.03);
  color: var(--muted);
  border: 1px solid rgba(255,255,255,0.03);
  padding: 6px 10px;
  border-radius: 8px;
  cursor: pointer;
}

.ultraWideToggleActive {
  background: linear-gradient(90deg, rgba(34,193,195,0.14), rgba(124,92,255,0.12));
  color: var(--text);
  border: 1px solid rgba(34,193,195,0.18);
}

/* Further tighten paddings inside ultraWide to free up more horizontal space */
.ultraWide .leftPane {
  padding: 2px;
}

.ultraWide .tableWrap {
  padding: 0;
}

/* hide any right-side detail pane content when ultraWide is active */
.ultraWide .rightPane {
  display: none !important;
}

/* ensure table uses full width without clipping issues */
.ultraWide .table {
  min-width: 0;
}

/* Keep trade and analysis layouts using the same column sizing to avoid
   jumps when switching tabs. Right pane remains present so widths are stable. */

/* Also handle alternative grid wrapper (.grid) and ensure no horizontal overflow occurs
   when analysis mode is active. Use box-sizing and min-width resets to avoid forcing
   the grid wider than the viewport. */
/* Avoid forcing single-column layout in analysis mode; keep grid-template consistent. */

.analysisActive .left,
.analysisActive .leftPane {
  box-sizing: border-box;
  min-width: 0;
}

.analysisActive .leftPane {
  /* keep visual styling, avoid dimensional overrides */
}

.analysisActive .leftPane .positionsList {
  /* keep default padding to avoid layout differences */
}

/* Ensure the container itself cannot cause viewport overflow when analysis mode is active */
.analysisActive {
  /* marker only — no dimensional overrides so tabs keep identical widths */
}

/* Use scoped universal box-sizing inside analysisActive to avoid padding from children causing overflow */
.analysisActive * {
  box-sizing: border-box;
  min-width: 0;
}

/* No transitions: keep instant switches and identical widths between tabs */

/* Note: modal-specific layout tweaks removed to keep the original centered modal behavior. */

.leftPane {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.01));
  border-radius: 10px;
  padding: 10px;
  min-height: 420px;
  box-shadow: 0 6px 24px rgba(2, 8, 23, 0.5);
  position: relative;
  z-index: 0; /* keep left pane under the right detail pane so shadows won't cover actions */
  overflow: hidden; /* clip large item shadows to left pane */
}

.rightPane {
  background: linear-gradient(180deg, rgba(10, 14, 18, 0.6), rgba(6, 10, 14, 0.4));
  border-radius: 10px;
  padding: 12px;
  min-height: 420px;
  box-shadow: 0 6px 24px rgba(2, 8, 23, 0.35);
  position: relative;
  z-index: 5; /* ensure right pane content (detail + actions) appear above left pane shadows */
}

/* tweak existing left/right panels to be darker and compact */
.left {
  background: linear-gradient(180deg, rgba(6, 10, 14, 0.95), rgba(6, 10, 14, 0.9));
  padding: 12px;
  border-radius: 12px;
}

.right {
  background: transparent;
  padding: 6px 0;
}

/* make table area darker and more subtle */
.table {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.005));
  border-radius: 8px;
  color: var(--text);
}

.table th,
.table td {
  color: var(--muted);
}

/* compact list area inside leftPane */
.positionsList {
  max-height: calc(68vh - 120px);
  overflow: auto;
  padding-right: 6px;
}

/* subtle scrollbar style for modern look (webkit browsers) */
.positionsList::-webkit-scrollbar {
  width: 8px;
}
.positionsList::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.04);
  border-radius: 6px;
}

/* small responsive adjustment */
@media (max-width: 980px) {
  .listAndDetailWrap {
    grid-template-columns: 1fr; /* stack vertically on small widths */
  }
  .leftPane {
    order: 1;
  }
  .rightPane {
    order: 2;
  }
}

/* Small and extra-small screens tweaks */
@media (max-width: 768px) {
  .listAndDetailWrap {
    gap: var(--space-3, 12px);
  }

  .grid {
    grid-template-columns: 1fr; /* stack columns for medium/small screens */
  }

  .left,
  .right,
  .leftPane,
  .rightPane {
    padding: var(--space-3, 12px);
    border-radius: calc(var(--card-radius, 10px));
  }

  /* Make the table area scroll horizontally instead of forcing wide min-width */
  .table {
    min-width: 0; /* allow table to shrink inside a scroll container */
  }
  .tableWrap {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }

  /* Make controls and buttons more touch-friendly */
  .controls,
  .filterPills,
  .actions {
    gap: var(--space-2, 8px);
  }

  .title {
    font-size: var(--fs-md, 16px);
  }
  .table th,
  .table td {
    font-size: var(--fs-sm, 14px);
  }
}

/* Very small screens (phones) - convert the list rows into stacked cards for better readability */
@media (max-width: 420px) {
  .positionsList {
    max-height: 50vh;
  }

  /* Hide table header and render rows as card-like blocks via CSS only fallback when possible */
  .table thead {
    display: none;
  }
  .table,
  .table tbody,
  .table tr,
  .table td {
    display: block;
    width: 100%;
  }

  .table tr {
    margin: 0 0 var(--space-3, 12px);
    border-radius: var(--card-radius, 8px);
    background: var(--card-bg, rgba(15, 23, 32, 0.6));
    box-shadow: var(--shadow-1, 0 6px 18px rgba(3, 6, 10, 0.6));
    padding: var(--space-3, 12px);
  }

  .table td {
    padding: 6px 0;
    border: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .symbolCell {
    gap: 10px;
  }
  .symbol {
    padding: 6px 8px;
    font-size: var(--fs-xs, 13px);
  }

  .secondaryDetailsRow {
    display: block;
  }

  /* Action button full width in card mode */
  .actions {
    justify-content: stretch;
  }
  .actions button {
    width: 100%;
  }

  /* Ensure the detail pane follows the list (stacked) */
  .leftPane,
  .rightPane {
    min-height: auto;
  }
}

.grid {
  display: grid;
  /* left column flexible but constrained, right column uses remaining space */
  /* moderate width so left content (list/cards) can use more horizontal room without overshooting */
  grid-template-columns: clamp(460px, 46vw, 820px) 1fr;
  gap: 18px;
  /* create isolated stacking context so z-index between columns behaves predictably */
  isolation: isolate;
}

/* When the parent grid container has the analysisActive class, widen the left column so the
   analysis form has more horizontal room. The class is placed on the same element as .grid. */
.grid.analysisActive {
  grid-template-columns: clamp(460px, 46vw, 820px) 1fr;
}

/* Ensure left column stays in the normal document flow by default so it does not overlay the table.
   The table will have a higher stacking context so table content isn't hidden by the left column. */
.left {
  display: grid;
  gap: 18px;
  min-width: 0;
  position: static; /* normal flow */
  z-index: auto;
  background: linear-gradient(180deg, rgba(7, 12, 18, 0.92), rgba(7, 12, 18, 0.88));
  padding: 14px;
  border-radius: 12px;
}

/* make sure direct children of .left (cards/forms) create their own stacking context when needed */
.left > * {
  position: static;
  z-index: auto;
  background-clip: padding-box; /* avoid shadows bleeding */
  min-width: 0; /* allow children to shrink properly in Firefox */
}

/* Remove the forced overlay in compact mode — compact will simply stack vertically without z-index tricks */
.grid.gridCompact .left {
  position: static;
  z-index: auto;
}

.grid.gridCompact .left > * {
  position: static;
  z-index: auto;
}

/* ensure right column can show overflow in compact mode (table will scroll under document flow) */
.grid.gridCompact .right {
  overflow: visible;
}

/* ensure right column content is clipped to its area so the table never visually overflows under the left column */
.right {
  display: grid;
  gap: 18px;
  min-width: 0; /* allow overflow within .tableWrap instead of pushing the whole grid */
  position: relative;
  z-index: 1;
  overflow: hidden; /* clip any visual overflow so left column is always unobstructed */
}

.form {
  display: grid;
  gap: 12px;
}

.row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  margin-top: 8px;
  flex-wrap: wrap; /* allow buttons to wrap on narrow screens */
}

.positionsPlaceholder {
  color: var(--muted);
  padding: 16px 0;
}

.headerRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 8px;
}

.title {
  font-size: 18px;
  font-weight: 700;
}

.controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.filterPills {
  display: flex;
  gap: 8px;
}

/* Make filter buttons pill-like and match the primary 'All' pill appearance
   without changing global Button styles. Scoped to trades filters and controls. */
.tradesFilters button,
.controls button {
  padding: 6px 10px;
  border-radius: 999px;
  font-weight: 700;
  min-height: 32px;
}

.tradesFilters button:hover,
.controls button:hover {
  transform: translateY(-1px);
}

/* lower the table shadow and make the table not visually bleed into left column */
.table {
  position: relative;
  z-index: 20;
  width: 100%;
  min-width: 0; /* allow table to shrink inside right pane so right card can use full width */
  border-collapse: collapse;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.005));
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(2, 8, 23, 0.28);
}

/* Ensure tableWrap creates a proper clipping/scrolling container */
.tableWrap {
  overflow: auto;
  max-height: 72vh; /* allow more vertical space */
  width: 100%;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 8px; /* breathing room for last row */
}

.table thead tr {
  border-bottom: 1px solid rgba(255, 255, 255, 0.03);
}

.table th {
  text-align: left;
  padding: 12px 10px;
  color: var(--muted);
  font-size: 13px;
  position: sticky;
  top: 0;
  background: rgba(4, 8, 12, 0.92);
  backdrop-filter: blur(6px);
  z-index: 25; /* stay above rows */
}

.table td {
  padding: 12px 10px;
  border-top: 1px solid rgba(255, 255, 255, 0.02);
  vertical-align: middle;
}

/* make sure expanded rows and hover states don't visually overlap sticky header */
.mainRow:hover {
  background: linear-gradient(90deg, rgba(14, 165, 164, 0.03), rgba(255, 255, 255, 0.01));
}

.mainRowExpanded {
  background: linear-gradient(90deg, rgba(14, 165, 164, 0.04), rgba(255, 255, 255, 0.02));
}

.mainRowExpanded td {
  position: relative; /* keep expanded content within stacking context */
}

.chevCell {
  width: 40px;
  text-align: center;
  padding: 0 6px;
}

.chev {
  color: var(--muted);
  font-size: 14px;
  display: inline-block;
}

/* reduce table padding slightly when expanded for compact look */
.mainRowExpanded td {
  padding-top: 16px;
  padding-bottom: 16px;
}

.symbolCell {
  display: flex;
  align-items: center;
  gap: 8px;
}

.symbol {
  padding: 6px 10px;
  background: rgba(14, 165, 164, 0.08);
  color: var(--text);
  border-radius: 8px;
  font-weight: 700;
  font-size: 13px;
}

.externalBtn {
  background: transparent;
  border: none;
  color: var(--muted);
  cursor: pointer;
  font-size: 14px;
}

.sideLong {
  color: var(--accent);
  font-weight: 700;
}

.sideShort {
  color: var(--danger);
  font-weight: 700;
}

/* small pill next to the side select to visually show LONG/SHORT color */
.sideBadge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.02);
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  min-width: 44px;
  text-align: center;
}

/* stronger visual background for badge variants */
.sideBadge.sideLong {
  background: rgba(105, 240, 174, 0.1);
  color: #69f0ae;
}
.sideBadge.sideShort {
  background: rgba(255, 107, 107, 0.1);
  color: #ff6b6b;
}

.plPositive {
  color: #7ef18a;
  font-weight: 700;
}

.plNegative {
  color: #ff6b6b;
  font-weight: 700;
}

.secondaryRow td {
  padding: 8px 10px 18px 10px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.003));
}

.secondaryInner {
  display: flex;
  gap: 18px;
  align-items: flex-start;
  justify-content: space-between;
}

.secondaryLeft {
  color: var(--muted);
  font-size: 13px;
  min-width: 220px;
}

.secondaryRight {
  color: var(--muted);
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 8px 22px;
  font-size: 13px;
}

.marketHeader {
  margin: 8px 0 12px 0;
  padding: 10px 12px;
  border-radius: 8px;
  background: linear-gradient(90deg, rgba(14, 165, 164, 0.02), rgba(255, 255, 255, 0.003));
  color: var(--muted);
  font-weight: 600;
}

.statusOpen {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(110, 231, 183, 0.08);
  color: #69f0ae;
  font-weight: 700;
  font-size: 12px;
}

.statusClosed {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.02);
  color: var(--muted);
  font-weight: 700;
  font-size: 12px;
}

/* Filled status (blue) */
.statusFilled {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
  background: rgba(203, 213, 225, 0.06);
  color: #60a5fa;
  font-weight: 700;
  font-size: 12px;
}

.filterPill {
  padding: 6px 10px;
  border-radius: 10px;
}

.secondaryDetailsRow {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 12px 18px;
  margin-bottom: 10px;
  font-size: 13px;
  color: var(--muted);
}

.secondaryNotesRow {
  grid-column: 1 / -1;
  background: rgba(14, 165, 164, 0.04);
  border-radius: 6px;
  padding: 10px 12px 8px 12px;
  font-size: 13px;
  color: var(--muted);
  margin-top: 2px;
}

.positionsList {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.positionItem {
  display: flex;
  align-items: center;
  gap: 14px;
  background: rgba(14, 165, 164, 0.06);
  border-radius: 6px;
  padding: 7px 10px;
  font-size: 13px;
  color: var(--muted);
}

.positionExpandRow {
  margin-top: 10px;
  padding: 10px 12px 8px 32px;
  background: rgba(14, 165, 164, 0.04);
  border-radius: 6px;
}

/* Normalize bold labels inside expanded rows to small/muted (Entry/SL/TPs) */
.positionExpandRow strong,
.secondaryDetailsRow strong,
.positionExpandRow .small,
.positionExpandRow .metaPiece,
.secondaryDetailsRow .small {
  font-weight: 400; /* normal */
  color: var(--muted);
}

/* Make the first-line inside positionExpandRow render like the small inline meta (Entry/SL/TPs)
   without requiring JSX changes: flex layout, small font and muted colour, no bold. */
.positionExpandRow > div {
  display: flex;
  gap: 12px;
  align-items: center;
  flex-wrap: wrap;
  font-size: 12px;
  color: var(--muted);
}
.positionExpandRow > div > div,
.positionExpandRow > div > span {
  font-size: 12px;
  color: var(--muted);
  font-weight: 400;
}
.positionExpandRow strong {
  font-weight: 400;
  color: var(--muted);
  margin-right: 4px;
}

.positionExpandGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr) 120px;
  gap: 10px 18px;
  align-items: end;
}

.tpAdvancedGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px 24px;
  align-items: start;
  margin-bottom: 8px;
}
.tpColLeft,
.tpColRight {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

@media (max-width: 700px) {
  .tpAdvancedGrid {
    grid-template-columns: 1fr;
    gap: 12px;
  }
  .tpColLeft,
  .tpColRight {
    flex-direction: row;
    gap: 12px;
  }
}

.input,
.tpInput {
  background: transparent;
  border: 1.5px solid transparent;
  border-radius: 4px; /* immer gleich, kein Sprung */
  color: var(--color-text, #e6e6e6);
  font-size: 1rem;
  font-family: inherit;
  padding: 0;
  outline: none;
  min-width: 60px;
  max-width: 100px;
  width: 100%;
  box-sizing: border-box;
  height: 24px;
  line-height: 24px;
  vertical-align: middle;
  transition: background 0.2s, box-shadow 0.2s, border-color 0.2s;
  text-align: left;
  appearance: none;
  -webkit-appearance: none;
}
.input:focus,
.tpInput:focus {
  background: rgba(255, 255, 255, 0.04);
  border-radius: 4px;
  border-color: var(--color-primary, #00bcd4);
  outline: none;
  box-shadow: none;
}
.table th,
.table td {
  min-width: 60px;
  box-sizing: border-box;
  height: 24px;
  line-height: 24px;
  vertical-align: middle;
}
.table th.symbolCell,
.table td.symbolCell {
  min-width: 90px;
  width: 90px;
}
.table th,
.table td {
  /* für Entry, SL, TP, Margin, Leverage feste Breite */
}
.table th:nth-child(5), .table td:nth-child(5), /* Entry */
.table th:nth-child(6), .table td:nth-child(6), /* SL */
.table th:nth-child(7), .table td:nth-child(7), /* Margin */
.table th:nth-child(8), .table td:nth-child(8), /* Leverage */
.table th:nth-child(9), .table td:nth-child(9)  /* P&L */ {
  min-width: 80px;
  width: 80px;
}

/* responsive tweaks */
@media (max-width: 900px) {
  .grid {
    grid-template-columns: 1fr;
  }

  .row {
    grid-template-columns: 1fr;
  }

  .table {
    min-width: 680px; /* allow horizontal scroll but keep columns readable */
  }

  .secondaryInner {
    flex-direction: column;
    gap: 8px;
  }
}

/* Ultra-wide tweaks: make table header slightly larger and increase gaps for readability */
@media (min-width: 1600px) {
  .table th {
    font-size: 14px;
    padding: 14px 12px;
  }

  .table td {
    padding: 14px 12px;
  }

  .secondaryRight {
    gap: 10px 32px;
  }
}

.gridCompact {
  grid-template-columns: 1fr !important;
}

/* When the grid is compact, stack left pane content and hide the right pane for mobile */
.grid.gridCompact .rightPane {
  display: none;
}

.grid.gridCompact .leftPane {
  width: 100%;
  min-width: 0;
}

/* ensure listAndDetailWrap behaves when compact: left pane should be full width */
.grid.gridCompact .listAndDetailWrap {
  display: block;
}

.slBeBtn {
  background: linear-gradient(90deg, #fef9c3 60%, #fef08a 100%);
  color: #665c00;
  font-weight: 700;
  font-size: 13px;
  border-radius: 16px;
  padding: 5px 16px;
  margin-right: 6px;
  margin-top: 2px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(250, 204, 21, 0.06);
  border: none;
  transition: background 0.18s, box-shadow 0.18s, transform 0.12s;
  text-decoration: none;
  outline: none;
}
.slBeBtn:hover {
  background: linear-gradient(90deg, #fef08a 60%, #fef9c3 100%);
  box-shadow: 0 4px 16px rgba(250, 204, 21, 0.1);
  transform: translateY(-2px) scale(1.04);
}
.slHitBtn {
  background: linear-gradient(90deg, #fee2e2 60%, #fecaca 100%);
  color: #991b1b;
  font-weight: 700;
  font-size: 13px;
  border-radius: 16px;
  padding: 5px 16px;
  margin-right: 6px;
  margin-top: 2px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.06);
  border: none;
  transition: background 0.18s, box-shadow 0.18s, transform 0.12s;
  text-decoration: none;
  outline: none;
}
.slHitBtn:hover {
  background: linear-gradient(90deg, #fecaca 60%, #fee2e2 100%);
  box-shadow: 0 4px 16px rgba(239, 68, 68, 0.1);
  transform: translateY(-2px) scale(1.04);
}
.analysisBadgeBtn {
  background: linear-gradient(90deg, #dbeafe 60%, #60a5fa 100%);
  color: #1e40af;
  font-weight: 700;
  font-size: 13px;
  border-radius: 16px;
  padding: 5px 16px;
  margin-right: 6px;
  margin-top: 2px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.06);
  border: none;
  transition: background 0.18s, box-shadow 0.18s, transform 0.12s;
  text-decoration: none;
  outline: none;
}
.analysisBadgeBtn:hover {
  background: linear-gradient(90deg, #60a5fa 60%, #dbeafe 100%);
  box-shadow: 0 4px 16px rgba(59, 130, 246, 0.1);
  transform: translateY(-2px) scale(1.04);
}

/* Ensure editor inside rightPane stretches nicely */
.rightPane .editor {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

/* make the right column card content scrollable when content grows */
.rightPane {
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow: hidden;
}

.rightPane .tableWrap,
.rightPane > * {
  overflow: auto;
}

/* keep a nice margin between list and detail on small screens */
@media (max-width: 980px) {
  .listAndDetailWrap {
    gap: 10px;
  }
  .leftPane,
  .rightPane {
    padding: 10px;
  }
}

/* Fullscreen variant for the main grid: use available viewport height and allow left/right panes to scroll independently */
.fullScreen {
  min-height: calc(100vh - 80px); /* account for app header bar height — adjust if needed */
  height: calc(100vh - 80px);
}

.fullScreen .left,
.fullScreen .right {
  min-height: 0; /* allow flex children to size correctly inside the grid */
}

/* make left and right columns scroll independently */
.fullScreen .left {
  overflow: auto;
  max-height: 100%;
}

.fullScreen .right {
  overflow: auto;
  max-height: 100%;
}

/* ensure inner cards stretch to fill column height when needed */
.fullScreen .left > *,
.fullScreen .right > * {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* ensure tableWrap inside right column scrolls properly when fullScreen */
.fullScreen .right .tableWrap {
  max-height: calc(100vh - 220px);
}

/* Trades header with inline filters */
.tradesHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin: 8px 0 12px 0;
}

.tradesTitle {
  font-size: 14px;
  font-weight: 700;
  color: var(--text);
}

.tradesControls {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Reserve space for the tabs + filter controls to avoid small layout shifts when
   switching between 'List' and 'Analyse' views. The min-width is tuned for
   desktop; on small screens the media query below reduces the constraint. */
.tradesControls {
  min-width: 420px;
}

/* Wrapper around the FilterToolbar inside TradeJournal. Reserve space so the
   filter/menu area doesn't shift when switching between List and Analyse. */
.marketFiltersWrap {
  min-width: 420px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.analysisControls {
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.analysisCreateBtn {
  border-radius: 12px;
  padding-inline: 16px;
  min-width: 150px;
  white-space: nowrap;
}

/* New: small row under the Trades title that contains the status filter pills (All/Open/Closed/Filled) */
.tradesStatusRow {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Ensure status pills inside the row render as small buttons by reusing filter styling and spacing */
.tradesStatusRow .button {
  /* leave visual styling to Button.module.css; ensure compact spacing */
  padding: 6px 10px;
  min-height: 34px;
}

.tradesFilters {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Tabs and wrapper for the trades area when not using Card */
.tradesArea {
  width: 100%;
  padding: 6px 6px 0 6px;
}

.tradesTabs {
  display: inline-flex;
  gap: 6px;
  align-items: center;
}

.tradesTab {
  background: transparent;
  border: 1px solid transparent;
  color: var(--muted);
  padding: 6px 10px;
  border-radius: 6px;
  cursor: pointer;
}

.tradesTabActive {
  color: var(--text);
  /* stronger accent for active tab: subtle gradient + soft glow to match pill style */
  background: linear-gradient(90deg, rgba(34,193,195,0.10), rgba(124,92,255,0.08));
  border-color: rgba(34,193,195,0.18);
  box-shadow: 0 6px 18px rgba(34,193,195,0.06), 0 0 0 1px rgba(124,92,255,0.04) inset;
  transform: translateY(-1px);
}

.tradesTab:focus-visible,
.tradesTabActive:focus-visible {
  outline: 3px solid rgba(124,92,255,0.12);
  outline-offset: 3px;
}

.tradesCount {
  margin-left: auto;
  color: var(--muted);
  font-size: 13px;
}

/* Responsive: stack header elements on small screens */
@media (max-width: 680px) {
  .tradesHeader {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  .tradesControls {
    width: 100%;
    min-width: 0; /* allow wrapping on small screens */
    display: flex;
    justify-content: space-between;
  }

  .marketFiltersWrap {
    min-width: 0; /* allow full-width wrapping on small screens */
    justify-content: space-between;
  }

  .analysisControls {
    width: 100%;
    flex-direction: column;
    align-items: stretch;
  }

  .analysisCreateBtn {
    width: 100%;
  }
}

/* New Trade form layout (left column) */
.newTradeWrapper {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.newTradeHeader {
  padding: 6px 8px;
}

/* two-column grid for form fields; collapses to single column on narrow widths */
.newTradeGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  align-items: start;
}

.newTradeField {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 0; /* allow children to shrink in Firefox */
}

/* full-width field (notes, etc.) */
.full {
  grid-column: 1 / -1;
}

/* make inputs fill their container and respect box model */
.input,
.tpInput,
input,
select,
textarea {
  width: 100%;
  box-sizing: border-box;
}

/* Align actions: Reset left, Add right */
.newTradeWrapper .actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

@media (max-width: 720px) {
  .newTradeGrid {
    grid-template-columns: 1fr;
  }
  .newTradeWrapper .actions {
    justify-content: flex-end;
  }
}

/* Mock data loader modal styles */
.backdrop {
  position: fixed;
  inset: 0;
  background: rgba(2, 6, 10, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.mockDialog {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.01));
  border-radius: 10px;
  padding: 18px;
  width: 520px;
  max-width: calc(100% - 40px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.6);
  color: var(--text);
}

.mockDialog h3 {
  margin: 0 0 8px 0;
  font-size: 16px;
}

.mockDialog p {
  margin: 0 0 12px 0;
  color: var(--muted);
}

.mockOption {
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.03);
  background: rgba(255, 255, 255, 0.01);
  color: var(--text);
  cursor: pointer;
}

.mockOption[aria-pressed='true'] {
  background: linear-gradient(90deg, rgba(124, 92, 255, 0.14), rgba(34, 193, 195, 0.06));
  color: var(--text);
  box-shadow: 0 6px 20px rgba(34, 193, 195, 0.06);
  transform: translateY(-1px);
}

/* Compact-mode helpers for stacked list+detail */
.compactSummary {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.005));
  border-radius: 8px;
  padding: 10px;
  box-shadow: 0 6px 18px rgba(2, 8, 23, 0.35);
}

.compactControls {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 8px;
}

.compactPlaceholder {
  padding: 12px;
  border-radius: 8px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.005));
  color: var(--muted);
}

/* subtle transition for editor area when toggling */
.leftPane > * {
  transition: max-height 220ms ease, opacity 180ms ease;
}

/* Advanced section (collapsed by default) */
.advanced {
  display: none;
  overflow: hidden;
  transition: max-height 220ms ease, opacity 180ms ease;
  max-height: 0;
  opacity: 0;
}
.advancedOpen {
  display: block;
  max-height: 800px; /* large enough to show children */
  opacity: 1;
}

.advancedToggle {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.04);
  color: var(--muted);
  padding: 6px 8px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 13px;
}

/* Mobile-specific compact layout for New Trade form */
@media (max-width: 480px) {
  .newTradeWrapper {
    padding: 8px 6px;
    gap: 8px;
  }

  .newTradeHeader {
    padding: 6px 0;
  }

  .newTradeGrid {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  .newTradeField {
    gap: 6px;
  }

  /* Make the New Trade card a scrollable panel so the rest of the page doesn't need long scrolling */
  .newTradeWrapper {
    max-height: calc(100vh - 120px); /* leave space for header and some margin */
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }

  /* Make inputs and selects larger for touch */
  input,
  select,
  textarea,
  .input {
    padding: 10px 12px;
    font-size: 15px;
  }

  /* Compact actions layout */
  .newTradeWrapper .actions {
    gap: 8px;
    justify-content: space-between;
  }
  .newTradeWrapper .actions button {
    flex: 1 1 auto;
  }
}

/* Mobile new trade quick button */
.mobileNewTradeBtnWrap {
  display: none;
  margin-bottom: 12px;
}
.mobileNewTradeBtn {
  display: inline-block;
  background: linear-gradient(90deg, var(--primary), color-mix(in srgb, var(--primary) 60%, black));
  color: var(--surface, #fff);
  border: none;
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 700;
}

/* Show mobile quick button on small screens */
@media (max-width: 480px) {
  .mobileNewTradeBtnWrap {
    display: block;
    margin-bottom: 10px;
  }
}

/* Mobile modal styles (fallback, inline styles used for main layout) */
.mobileModalBackdrop {
  display: none;
}
.mobileModalContent {
  display: block;
}

/* Mobile sheet/modal styles */
.mobileModalBackdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: flex-end; /* sheet comes from bottom */
  justify-content: center;
  z-index: 2000;
}

@keyframes sheetSlideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.mobileModalContent {
  width: 100%;
  max-width: 760px;
  background: var(--card-bg, #0f1720);
  border-radius: 12px 12px 0 0; /* rounded top corners for sheet */
  padding: 18px 16px 28px 16px;
  box-shadow: 0 -8px 40px rgba(0, 0, 0, 0.6);
  animation: sheetSlideUp 260ms cubic-bezier(0.2, 0.8, 0.2, 1) both;
  max-height: 92vh;
  overflow: auto;
}

/* On slightly larger mobile screens center the modal instead */
@media (min-width: 421px) and (max-width: 480px) {
  .mobileModalBackdrop {
    align-items: center;
  }
  .mobileModalContent {
    border-radius: 12px;
    width: 90vw;
    max-width: 420px;
  }
}

/* For the close button ensure it's visible over the sheet */
.mobileModalContent > button[aria-label='Close'],
.mobileModalContent > button[aria-label='Filled'] {
  z-index: 10;
  color: var(--text);
}

/* make the header area inside modal sticky so user can see title when scrolling the sheet */
.mobileModalContent > div:first-child {
  position: sticky;
  top: 0;
  background: transparent;
  padding-bottom: 8px;
}

/* Mobile sheet footer: sticky action bar */
.mobileModalFooter {
  position: sticky;
  bottom: 0;
  display: flex;
  gap: 12px;
  justify-content: space-between;
  padding: 12px 8px 8px 8px;
  background: linear-gradient(180deg, rgba(15, 23, 32, 0), rgba(15, 23, 32, 0.6));
  backdrop-filter: blur(6px);
  z-index: 30;
}

/* Ensure .actions inside the form are hidden when shown inside the mobile sheet to avoid duplicates */
@media (max-width: 480px) {
  .mobileModalContent .actions {
    display: none !important;
  }
}

/* TP row inside NewTradeForm — distribute TP1..TP4 evenly and collapse to 2 columns on small screens */
.tpRow {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
  align-items: start;
}

@media (max-width: 520px) {
  .tpRow {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Two-column TP layout used by NewTradeForm advanced section
   - Left column: TP1 & TP2 (side-by-side)
   - Right column: TP3 & TP4 (stacked under SL, i.e. under the right column)
   Notes field uses .full (grid-column: 1 / -1) and will appear below these columns. */
.tpGridTwoCol {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  align-items: start;
}

.tpLeftRow {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  align-items: start;
}

.tpRightCol {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: stretch;
}

/* On narrow screens stack the two columns and keep TP1/TP2 side-by-side (2 cols) while TP3/TP4 remain stacked under it */
@media (max-width: 720px) {
  .tpGridTwoCol {
    grid-template-columns: 1fr; /* stack left + right vertically */
  }
  .tpLeftRow {
    grid-template-columns: repeat(2, 1fr); /* keep TP1/TP2 side-by-side when stacked */
  }
}

/* Utility classes used by TradeJournal.tsx to avoid inline styles */
.tradesHeaderColumn {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.spacer12 {
  height: 12px;
}

.compactSummaryInner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.strong {
  font-weight: 700;
}

.mutedSmall {
  color: var(--muted);
  font-size: 13px;
}

.compactPlaceholderTitle {
  font-weight: 700;
  margin-bottom: 6px;
}

.mutedText {
  color: var(--muted);
}
