.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  padding: 14px 18px;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.01));
  border-radius: 10px;
  position: relative;
}

.brand {
  font-weight: 900;
  letter-spacing: 0.3px;
  font-size: 20px;
  color: var(--text);
}

.nav {
  display: flex;
  gap: 12px;
}

.nav a {
  color: var(--muted);
  text-decoration: none;
  font-weight: 600;
}

.nav a:hover {
  color: var(--text);
}

.controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Auth controls */
.authWrap {
  display: flex;
  align-items: center;
  gap: 8px;
}
.authBtn {
  border: none;
  border-radius: 10px;
  padding: 8px 12px;
  font-weight: 700;
  cursor: pointer;
}
.login {
  background: linear-gradient(180deg, #ffd27a 0%, #ffb84e 100%);
  color: #1a1400;
}
.logout {
  background: linear-gradient(180deg, #ff7a7a 0%, #ff4e4e 100%);
  color: #1a0000;
}
.authBtn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.authError {
  background: rgba(255, 84, 84, 0.12);
  border: 1px solid rgba(255, 84, 84, 0.35);
  color: #ffb7b7;
  border-radius: 8px;
  padding: 6px 10px;
  font-size: 12px;
}

.userBadge {
  position: relative;
}
.userBtn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: transparent;
  border: none;
  color: var(--text);
  cursor: pointer;
  padding: 6px 8px;
  border-radius: 10px;
}
.userBtn:hover {
  background: rgba(255, 255, 255, 0.06);
}
.avatar {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  object-fit: cover;
}
.avatarFallback {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.08);
  color: var(--muted);
  font-weight: 800;
}
.userName {
  max-width: 160px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.userMenu {
  position: absolute;
  right: 0;
  top: 44px;
  display: none;
  background: var(--card-bg-solid, rgba(10, 12, 15, 1));
  border-radius: 10px;
  box-shadow: var(--shadow-1, 0 10px 30px rgba(0, 0, 0, 0.6));
  padding: 8px;
  z-index: 3200;
}
.userMenuOpen {
  display: block;
}

.hamburger {
  display: none;
  background: transparent;
  border: none;
  color: var(--text);
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.outboxBadgePlaceholder {
  display: inline-flex;
  align-items: center;
  margin-right: 4px;
}

@media (max-width: 720px) {
  .nav {
    display: none;
  }
  .hamburger {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
}

/* When mobile nav is opened (JS toggles .open) show as vertical list */
.mobileNav {
  display: none;
}
.mobileOpen {
  display: block;
  position: absolute;
  top: 56px;
  right: 12px;
  /* Use a solid background to avoid transparency issues on dark headers */
  background: var(--card-bg-solid, rgba(10, 12, 15, 1));
  border-radius: 8px;
  padding: 8px;
  box-shadow: var(--shadow-1, 0 10px 30px rgba(0, 0, 0, 0.6));
  z-index: 3000;
}
.mobileNav a {
  display: block;
  padding: 8px 12px;
  color: var(--text);
  text-decoration: none;
}

/* ensure focus styles inside mobile nav */
.mobileNav a:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(88, 214, 141, 0.12);
}

/* ensure hamburger button is always above nav for click handling */
.hamburger {
  z-index: 3100;
}

/* backdrop behind mobile nav */
.backdrop {
  display: none;
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  z-index: 2500;
  opacity: 0;
  transition: opacity 220ms ease;
}
.backdropVisible {
  display: block;
  opacity: 1;
}

/* slide-in mobile nav */
.mobileNav {
  display: none;
  transform: translateY(-8px) translateX(8px) scale(0.98);
  opacity: 0;
  transition: transform 220ms ease, opacity 180ms ease;
}
.mobileOpen {
  display: block;
  transform: translateY(0) translateX(0) scale(1);
  opacity: 1;
}
